export function formatDate (val) {
  if (!val) {
    return ''
  }
  val = val.toString()
  let y = val.slice(0, 4)
  let m = val.slice(4, 6)
  let d = val.slice(6)
  let date = new Date(y, m - 1, d)
  let arr = ['', '', '', '', '', '', '']
  return `${y}${m}${d} ${arr[date.getDay()]}`
}



